.mate-checker {
	height: 100%;
	width: 100%;
	@include flexCenter;

	&__main {
		@include flexColumn;
	}

	&__container {
		display: grid;
		justify-content: center;
		align-items: center;
		grid-template-columns: 1fr 1fr 1fr;
	}

	&__figures {
		transition: 1s;
		display: grid;
		grid-template-rows: repeat(3, 1fr);
		grid-template-columns: repeat(2, 1fr);
		gap: 2rem;

		justify-self: end;
		margin-right: 3.5rem;
		:last-child {
			grid-row: 3/4;
			grid-column: 1/3;
		}
	}

	&__control-btns {
		@include flexColumnCenter;
	}
}
